@import "https://fonts.googleapis.com/css2?family=Kanit:ital,wght@0,400;0,700;1,300&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
}

p {
  padding: 0;
}

body {
  height: 100vh;
  background-image: url("bg.9c2eeda7.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: Kanit, sans-serif;
  display: flex;
  position: relative;
  overflow: hidden;
}

.breed-select {
  z-index: 10;
  height: 50px;
  width: 250px;
  color: #000;
  cursor: pointer;
  background-color: rgba(255, 255, 255, .8);
  border-radius: 10px;
  padding: 10px;
  font-family: Kanit, sans-serif;
  font-size: 20px;
  font-weight: 700;
  transition: all .5s linear;
  position: absolute;
  top: 12%;
  left: 1%;
}

.show-dogs {
  z-index: 10;
  height: 50px;
  width: 250px;
  color: #000;
  cursor: pointer;
  background-color: rgba(255, 255, 255, .8);
  border-radius: 10px;
  padding: 5px;
  font-family: Kanit, sans-serif;
  font-size: 20px;
  font-weight: 700;
  transition: all .5s linear;
  position: absolute;
  top: 5%;
  left: 1%;
}

.breed-select:hover, .show-dogs:hover {
  background-color: #fff;
}

.cat-card {
  max-width: 600px;
  max-height: 800px;
  -webkit-backdrop-filter: blur(25px);
  backdrop-filter: blur(25px);
  background-color: rgba(255, 255, 255, .2);
  border-radius: 20px;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  display: flex;
}

.cat-img {
  width: 200px;
  max-height: 250px;
  height: auto;
  object-fit: cover;
  border-radius: 20px;
  transition: all .3s;
  transform: scale(.95);
}

.cat-card:hover .cat-img {
  transform: scale(1);
}

.cat-name {
  letter-spacing: 5px;
  font-size: 30px;
  font-weight: 700;
  font-style: bold;
}

.cat-description {
  font-size: 18px;
}

.cat-temperament {
  font-size: 20px;
  font-style: italic;
  font-weight: 300;
}

.cat-temperament-title {
  font-style: normal;
}

.is-hidden {
  visibility: hidden;
}

.backdrop {
  z-index: 100;
  width: 100%;
  height: 100%;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  background-color: rgba(0, 0, 0, .5);
  transition: opacity .5s linear, visibility .5s linear;
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-open {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
}

.modal {
  min-width: 600px;
  min-height: 600px;
  background-color: bisque;
  border: 2px solid rgba(255, 255, 255, .5);
  border-radius: 20px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.img-cat-hypnosis {
  object-fit: cover;
  height: 380px;
}

.popup {
  z-index: 999;
  background-color: #f9f9f9;
  border: 1px solid #ccc;
  padding: 20px;
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 2px 8px rgba(0, 0, 0, .1);
}

.want-dogs-title {
  color: #e91e63;
  margin-bottom: 30px;
  font-size: 36px;
}

.buttons {
  margin: 20px;
}

.dogs-btn {
  color: #fff;
  cursor: pointer;
  background-color: #ff4081;
  border: none;
  border-radius: 20px;
  margin: 5px;
  padding: 12px 24px;
  font-size: 20px;
  transition: background-color .3s;
}

button:hover {
  background-color: #ff5c8d;
}

.popup {
  z-index: 11;
  color: #e91e63;
  background-color: #fff;
  border: 1px solid #e91e63;
  border-radius: 10px;
  padding: 10px;
  font-family: Dancing Script, cursive;
  font-size: 24px;
  display: none;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 5px 15px rgba(0, 0, 0, .2);
}

.popup.fade-in {
  animation: fadeIn .3s;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.heading {
  font-family: eight, sans-serif;
  font-style: normal;
  font-weight: 400;
}

.large-text {
  font-size: 5rem;
}

#hero {
  color: #fff;
  background: #101010;
  padding: 2rem;
}

#hero:after {
  content: "";
  width: 100vw;
  height: 5rem;
  background: linear-gradient(2deg, #101476 0%, #101476 50%, #101010 50%, #101010 100%);
  display: block;
  position: absolute;
  left: 0;
}

#loading-animation {
  width: 100vw;
  height: 100vh;
  text-align: center;
  color: #fff;
  background: rgba(0, 0, 0, .486);
  padding-top: 40vh;
  position: absolute;
}

#loading-animation p {
  animation: bounce 1s ease-in-out infinite;
  display: inline-block;
}

#loading-animation p:nth-of-type(2) {
  animation-delay: .1s;
}

#loading-animation p:nth-of-type(3) {
  animation-delay: .2s;
}

#loading-animation p:nth-of-type(4) {
  animation-delay: .3s;
}

#loading-animation p:nth-of-type(5) {
  animation-delay: .4s;
}

#loading-animation p:nth-of-type(6) {
  animation-delay: .5s;
}

#loading-animation p:nth-of-type(7) {
  animation-delay: .6s;
}

@keyframes bounce {
  30% {
    transform: translateY(-100px);
  }

  60% {
    transform: none;
  }
}

